import React,{useState} from 'react';
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import './MMSLogistic.scss'

// Logistic section
import LogisticIntro from '../../Logistis/LogisticIntro'
import LogisticRule from '../../Logistis/LogisticRule'
import LogisticTalk from '../../Logistis/LogisticTalk'
import LogisticComplaint from '../../Logistis/LogisticComplaint'
import LogisticShip from '../../Logistis/LogisticShip'

function MMSLogistic(props) {
    const [value, setValue] = useState('1');

    const handleChange = (event, newValue) => {
      setValue(newValue);
    };

    return (
        <div className="logistic">
            <Box sx={{ width: '100%', typography: 'body1' }}>
            <TabContext value={value}>
                <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                <TabList onChange={handleChange} aria-label="lab API tabs example">
                    <Tab label="Gii thiu" value="1" />
                    <Tab label="Quy nh" value="2" />
                    <Tab label="Hi p" value="3" />
                    <Tab label="Khiu ni" value="4" />
                    <Tab label="Vn n" value="5" />
                </TabList>
                </Box>
                
                {/* Gii thiu */}
                <TabPanel value="1">
                    <LogisticIntro/>
                </TabPanel>
                
                {/* Quy nh */}
                <TabPanel value="2">
                    <LogisticRule/>
                </TabPanel>
                
                {/* Hi p */}
                <TabPanel value="3">
                    <LogisticTalk/>
                </TabPanel>
                
                {/* Khiu ni */}
                <TabPanel value="4">
                    <LogisticComplaint/>
                </TabPanel>
                
                {/* Vn n */}
                <TabPanel value="5">
                    <LogisticShip/>
                </TabPanel>

                
            </TabContext>
            </Box>
        </div>
    );
}

export default MMSLogistic;